
* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 30px;;
}

input {
  color: #0a0a00;
  font-size: 1.3rem;;
  width: 100%;

}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
}